@import './src/common/styles/variables';

.aboutUs {
  min-height: 100%;
  background-color: $bgColor;
  display: flex;
  align-items: center;
}

.aboutUsContainer {
  justify-content: space-between;
  flex-wrap: wrap;
  min-height: 100%;
  flex-direction: column;
  padding-top: 50px;
  padding-bottom: 30px;
}

.photo img {
  border-radius: 15px;
  object-fit: cover;
  object-position: center;
  width: 400px;

  &:before {
    content: '';
    height: 110%;
    width: 105%;
    border: 5px solid $primaryColor;
    left: 15px;
    top: -15px;
    z-index: -1;
  }
}

.info {
  color: white;
  width: 450px;
  font-weight: 500;
  text-align: justify;
  font-size: 21px;
  margin-left: 20px;
}

.content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
  width: 80%;
}